import Vue from "vue";
import App from "./App.vue";
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import ua from "universal-analytics";
import VueI18n from "vue-i18n";
import messages from "./message";

global.browser = require("webextension-polyfill");

const extGa = ua("UA-187200210-2");

let getUid = async function () {
  let rest = await browser.storage.local.get("greenhubExt");
  let uid = null;
  if (rest && rest.greenhubExt && rest.greenhubExt.uid) {
    uid = rest.greenhubExt.uid;
  }
  return uid;
};
getUid().then((uid) => {
  extGa.set("uid", uid);
});

Vue.prototype.$ga = extGa;

Vue.use(ElementUI);
Vue.use(VueI18n);

// 通过选项创建 VueI18n 实例
const i18n = new VueI18n({
  locale: browser.i18n.getUILanguage(), // 设置地区
  messages, // 设置地区信息
});

/* eslint-disable no-new */
new Vue({
  i18n,
  el: "#app",
  render: (h) => h(App),
});
