<script>
import axios from "axios"
import Vue from "vue"
import dataList from "./data_list.json"

export default {
  name: "App",
  components: {},
  data() {
    return {
      // 插件基本信息
      extInfo: {
        version: "0.9",
        uid: "",
        connection: null,
        sharedDate: null,
        popupTime: null,
        pcDialogTime: null,
      },
      // 最新版本
      remoteVersion: "",
      // 是否显示更新弹窗
      dialogUpdateShow: false,
      hasUpdate: false,
      // 总连接人数
      totalConnections: "...",
      // 是否显示分享弹窗
      popupDivShow: false,
      shareType: 1, //1是没有分享的状态 2是qq分享中 3是微博分享中 4是分享完成
      proxies: [],
      proxiesLoading: true,
      // 辅助变量
      today: new Date(),
      popupStartShow: false,
      popupStartNotShowCheckout: false,
      popupStartNeedShow: false,
      isFirstUse: false,
      isShowThisTime: false,
      dialogDnsTips: false,
      language: "us",
      isHover: false, //鼠标是否经过按钮
      count: "", //倒计时
      PcDialog: false, //导流弹窗一开始默认关闭
      pcDialogShowTime: 3, //导流弹窗默认三天显示弹出一次
      ghDesktopDownloadPage: "https://greenhubtx.ga",
      seconds: 1800, // 剩余的秒数
      connectEveryMinutes: 30,
      connectMaxMinutes: 60,
      connectRest: -1,
    }
  },
  methods: {
    // 获取插件基本信息
    async getExtInfo() {
      let resp = await browser.runtime.sendMessage({
        action: "extInfo",
        data: {},
      })
      this.extInfo = {
        ...this.extInfo,
        ...resp,
      }
      if (this.extInfo.connection) {
        //获取连接时候的时间戳，用现在的时间戳减去连接的时间戳，计算出现在已经连接的时间
        this.seconds =
          (this.connectEveryMinutes * 60 * 1000 -
            (new Date().getTime() - this.extInfo.connection.timestamp)) /
          1000
        this.countDown()
        clearInterval(this.timer)
        this.Time()
      }
      this.extInfo.sharedDate =
        this.extInfo.sharedDate === null
          ? null
          : new Date(this.extInfo.sharedDate)
      this.extInfo.popupTime =
        this.extInfo.popupTime === null
          ? null
          : new Date(this.extInfo.popupTime)
      this.extInfo.pcDialogTime =
        this.extInfo.pcDialogTime === null
          ? null
          : new Date(this.extInfo.pcDialogTime)
      console.debug("getExtInfo: getting basic information: ", this.extInfo)
    },
    // 解析 html 特殊字符，用于解析 github 返回的字符串
    decodeHtml(html) {
      let txt = document.createElement("textarea")
      txt.innerHTML = html
      return txt.value
    },
    // 获取代理节点信息
    async requestProxiesUrl(url, token, anotherCancelToken, type) {
      try {
        console.debug("fetching url: ", url)
        const rst = await axios.get(url, {
          cancelToken: token,
          responseType: "text",
        })
        console.debug("fetch ", type, " status: ", rst)
        if (rst.status !== 200) {
          return
        }
        // 取消另一个请求
        anotherCancelToken.cancel()
        let data = {}
        switch (type) {
          case "aws":
            data = rst.data
            break
          case "github":
            data = JSON.parse(
              this.decodeHtml(rst.data).match(/@@@@(.*)&&&&/)[1]
            )
            break
        }
        const self = this
        // 节点列表的多语言处理
        if (data.nodes) {
          data.nodes.forEach((node) => {
            // 中文
            if (self.language && self.language.indexOf("zh") >= 0) {
              if ("name_zh" in node) {
                node.name = node.name_zh
              }
            } else if ("name_en" in node) {
              // 默认为英语
              node.name = node.name_en
            }
          })
        }
        this.proxies = data.nodes ? data.nodes : []
        this.remoteVersion = data.version ? data.version : this.extInfo.version
      } catch (e) {
        if (axios.isCancel(e)) {
          console.debug("e is cancellation")
        }
        console.debug("error: ", e)
      }
    },
    async fetchProxies() {
      if (process.env.NODE_ENV === "development") {
        this.proxies = dataList.nodes
        this.remoteVersion = dataList.version
          ? dataList.version
          : this.extInfo.version
        this.proxiesLoading = false
        return
      }
      this.proxiesLoading = true
      console.debug("fetching proxies...")
      let nameJson = this.isEdgePlatform()
        ? "data_list_v4_edge_gh.json"
        : "data_list_v4_ce_gh.json"

      // US04
      const proxiesUrl = `https://static.greenhubex.ml/${nameJson}?t=${new Date().getHours()}&uid=${
        this.extInfo.uid
      }&version=${this.extInfo.version}`
      // jp06
      const bakProxiesUrl = `https://static.greenhubtx.ga/${nameJson}?uid=${this.extInfo.uid}&version=${this.extInfo.version}`
      let cancelTokenAws = axios.CancelToken.source()
      let cancelTokenGithub = axios.CancelToken.source()
      await Promise.allSettled([
        this.requestProxiesUrl(
          proxiesUrl,
          cancelTokenAws.token,
          cancelTokenGithub,
          "aws"
        ),
        this.requestProxiesUrl(
          bakProxiesUrl,
          cancelTokenGithub.token,
          cancelTokenAws,
          "aws"
        ),
      ])
      this.proxiesLoading = false
    },
    show() {
      this.$ga.event("Bubble_downloadButton_show_count", "show_count").send()
    },
    // ip 地址脱敏
    ipSec(ipAddr) {
      let arr = ipAddr.split(".")
      arr[1] = arr[2] = "***"
      return arr.join(".")
    },
    // 获取拥挤程度 节点状态信息
    async fetchStatus(proxy, i) {
      try {
        const rst = await axios.post(
          `${proxy.host}/status?uid=${this.extInfo.uid}&v=${this.extInfo.version}`,
          {
            f: this.extInfo.uid,
            v: this.extInfo.version,
          }
        )
        if (rst.status !== 200) {
          proxy.capacity = 0
          proxy.current = 1
          proxy.enabled = false
          this.$ga.event("connectReq/failed", proxy.name).send()
        } else {
          proxy.capacity = rst.data.capacity
          proxy.current = rst.data.current
          if (rst.data.enabled != undefined) {
            proxy.enabled = rst.data.enabled
          } else {
            proxy.enabled = false
          }
          this.$ga.event("connectReq/success", proxy.name).send()
          proxy.congestion = this.calCongestion(proxy)
        }
      } catch (err) {
        this.$ga.event("connectReq/failed", proxy.name).send()
        proxy.capacity = 0
        proxy.current = 1
        proxy.congestion = "error"
      }
      // proxy.congestion = this.calCongestion(proxy)
      Vue.set(this.proxies, i, proxy)
      if (
        proxy.congestion === "full" ||
        proxy.congestion === null ||
        proxy.congestion === "error"
      ) {
        this.$ga
          .event("ServerDown_downloadButton_show_count", "err_count")
          .send()
      }
      return proxy
    },
    // 计算拥挤程度
    // full, high, medium-high, medium-low, low
    calCongestion(proxy) {
      if (proxy.capacity <= 0) {
        console.debug("总容量小于 0")
        return "error"
      }
      //连接人数/节点容量
      const level = parseInt(((proxy.current / proxy.capacity) * 10).toFixed(0))
      switch (level) {
        case 0:
        case 1:
          return "low"
        case 2:
        case 3:
        case 4:
          return "medium-low"
        case 5:
        case 6:
          return "medium-high"
        case 7:
        case 8:
          return "high"
        case 9:
        case 10:
        default:
          return "full"
      }
    },
    // 检测更新
    checkUpdate(current, remote) {
      console.debug("checking updates...", current, remote)
      current = current.split(".")
      remote = remote.split(".")

      if (parseInt(remote[0]) > parseInt(current[0])) {
        return true
      }
      if (parseInt(remote[1]) > parseInt(current[1])) {
        return true
      }
      if (parseInt(remote[2]) > parseInt(current[2])) {
        return true
      }
      return false
    },
    // 进入页面后的启动函数
    async init() {
      console.debug("option page initiating...")
      // 获取当前插件基本信息，版本号，uid, 分享状态 等
      await this.getExtInfo()

      await this.getPcDialogSettings()

      // 获取节点列表
      await this.fetchProxies()
      this.totalConnections = 0
      // 检测到有更新
      if (this.checkUpdate(this.extInfo.version, this.remoteVersion)) {
        this.dialogUpdateShow = true
        this.hasUpdate = true
      }
      // this.hasUpdate = true
      // this.dialogUpdateShow = true

      if (!this.proxies) {
        this.$ga
          .pageview("Unavailable_downloadlink_show_count", "none_count")
          .send()
      }

      // 统计各节点人数，并发送给 background 进行同步
      for (let i = 0; i < this.proxies.length; i++) {
        // 如果节点不可用，则跳过
        if (this.proxies[i].type !== "node") {
          continue
        }
        // if (process.env.NODE_ENV === 'development') {
        //   this.proxies[i].enabled = true
        // }
        if (this.proxies[i].enabled == false) {
          continue
        }
        this.$set(this.proxies[i], "loading", true)
        this.$set(this.proxies[i], "connecting", false)
        this.fetchStatus(this.proxies[i], i).then((rst) => {
          this.totalConnections += rst.current
          this.$set(this.proxies[i], "loading", false)
          browser.runtime.sendMessage({
            action: "setTotalConnections",
            data: this.totalConnections,
          })
        })
      }
      // popupStartNotShowAgain  判断是否需要再次弹出评论窗口 popupStartNeedShow
      const self = this
      browser.storage.local
        .get(["popupStartNotShowAgain", "firstUse"])
        .then(function (results) {
          self.isFirstUse = results.firstUse
          if (results.popupStartNotShowAgain) {
            self.popupStartNeedShow = false
          }
        })
    },
    // 检测弹出情况
    async checkStartStatus() {
      const now_time = new Date().getTime() //获取现在的时间戳
      // 只有现在的时间戳比下载弹窗的时间戳多一天的时候才可以显示好评弹窗
      const popupCanShow =
        now_time - this.extInfo.pcDialogTime.getTime() >= 24 * 60 * 60 * 1000
      this.popupStartShow =
        this.popupStartNeedShow && !this.isShowThisTime && popupCanShow

      browser.storage.local.set({ popupStartShowSuccess: true })
      this.isShowThisTime = true
    },
    // 连接代理
    async connect(proxy) {
      // TODO 链接状态
      // this.$set(  this.proxies[i], 'connecting', false)
      this.$set(proxy, "connecting", true)
      const resp = await browser.runtime.sendMessage({
        action: "connect",
        data: proxy,
      })
      this.$set(proxy, "connecting", false)
      if (!resp) {
        alert("链接失败，请稍后重试")
      } else {
        this.getExtInfo().then()

        browser.storage.local.set({ firstUse: false })
        if (this.isFirstUse == false && this.popupStartNeedShow) {
          this.popupStartShow = true
        }
      }
      this.$ga.event("connect/click", proxy.name).send()
      console.debug("connected: ", resp)
      if (proxy.congestion === "high") {
        this.$ga.event("Bubble_continue_click_count", "continue").send()
      }
    },
    async disconnect() {
      //解决有时候连接后显示断开的问题
      this.isHover = false
      await browser.runtime.sendMessage({
        action: "disconnect",
        data: {},
      })
      clearInterval(this.timer)
      this.init().then()
    },
    // 跳转到更新页面
    gotoUpdatePage() {
      window.open("https://github.com/pablocc1979/Green-Hub-Proxy")
      this.$ga.event("click/githubproxy", "main").send()
    },
    openGithubDesktopRelease(e) {
      window.open(this.ghDesktopDownloadPage)
      // 分别统计
      this.$ga.event(e, "click").send()
      // 所有下载按钮统计
      this.$ga.event("click/githubdesktop", "releases").send()
    },
    // 当有更新时，模糊节点列表
    blurIfHasUpdate({ row, rowIndex }) {
      let clName = ""
      if (rowIndex > 1 && this.hasUpdate) {
        clName = "vague"
      }
      //  分隔单元格高亮
      if (row.type === "division") {
        clName = clName + " table-division"
      }
      return clName
    },
    shareQQ() {
      this.shareType = 2
      let title =
        "个人推荐：GreenHub 绿墙，五星好用的网络出海工具，500+五星好评。"
      let url = "https://github.com/pablocc1979/Green-Hub-Proxy"
      let picurl = "../assets/picture1.png"
      let shareqqzonestring =
        "http://sns.qzone.qq.com/cgi-bin/qzshare/cgi_qzshare_onekey?summary=" +
        title +
        "&url=" +
        url +
        "&pics=" +
        picurl
      this.$ga.event("share/qq/click", "click").send()
      window.open(
        shareqqzonestring,
        "newwindow",
        "height=400,width=400,top=100,left=100"
      )
      let that = this
      setTimeout(function () {
        browser.runtime
          .sendMessage({
            action: "userShared",
            data: {},
          })
          .then(() => {
            that.getExtInfo().then()
          })
        that.shareType = 4
      }, 5000)
    },
    shareToXl() {
      this.shareType = 3
      let title =
        "个人推荐：GreenHub 绿墙，五星好用的网络出海工具，500+五星好评。"
      let url = "https://github.com/pablocc1979/Green-Hub-Proxy"
      let picurl = "../assets/picture1.png"
      let sharesinastring =
        "http://v.t.sina.com.cn/share/share.php?title=" +
        title +
        "&url=" +
        url +
        "&content=utf-8&sourceUrl=" +
        url +
        "&pic=" +
        picurl
      this.$ga.event("share/sina/click", "click").send()
      window.open(
        sharesinastring,
        "newwindow",
        "height=400,width=400,top=100,left=100"
      )
      let that = this
      setTimeout(function () {
        browser.runtime
          .sendMessage({
            action: "userShared",
            data: {},
          })
          .then(() => {
            that.getExtInfo().then()
          })
        that.shareType = 4
      }, 5000)
    },
    shareQQWeibo() {
      this.popupDivShow = true
    },
    closeDiv() {
      this.popupDivShow = false
    },
    // 帮助函数
    sleep(ms) {
      return new Promise((resolve) => setTimeout(resolve, ms))
    },
    // 下次再说按钮响应事件
    onNextToSay() {
      this.popupStartShow = false
      if (this.popupStartNotShowCheckout) {
        browser.storage.local.set({ popupStartNotShowAgain: true })
      }
    },
    // 打开商店页面
    openChromeStorePage() {
      var homePageUrl = process.env.VUE_APP_HOMEPAGE_URL
      window.open(homePageUrl)
    },
    // 检查节点是否加载失败，以弹窗提示
    checkErrorForDnsTips() {
      let show = true
      for (let i = 0; i < this.proxies.length; i++) {
        // 如果节点不可用，则跳过
        // congestion = 'error'
        if (
          !(this.proxies[i].congestion && this.proxies[i].congestion == "error")
        ) {
          show = false
        }
      } // end for
      this.dialogDnsTips = show
    },
    // 合并单元格
    // eslint-disable-next-line no-unused-vars
    arraySpanMethod({ row, column, rowIndex, columnIndex }) {
      if (row.type === "division" && columnIndex === 0) {
        return [1, 4]
      } else if (row.type === "division" && columnIndex in [1, 2, 3]) {
        return [0, 0]
      }
    },
    // 改变按钮的hover状态
    changeHover(isHover) {
      this.isHover = isHover
    },
    //  分 秒 格式化函数
    countDown() {
      let m = parseInt((this.seconds / 60) % 60)
      m = m < 10 ? "0" + m : m
      let s = parseInt(this.seconds % 60)
      s = s < 10 ? "0" + s : s
      this.count = m + ":" + s
    },
    //定时器每过1秒参数减1
    Time() {
      this.timer = setInterval(async () => {
        this.seconds -= 1
        if (this.isEdgePlatform()) {
          this.getCurRestTime()
        }
        this.countDown()
      }, 1000)
    },
    // 更新导流弹窗配置项中的数据
    getPcDialogSettings() {
      axios
        .get("https://static.greenhubtx.ga/gh_config.json")
        .then((response) => {
          //如果配置文件中需要弹窗，则更新配置中的弹窗打开时间
          if (response.data.data.popup_gh_desktop_download.need_show) {
            this.pcDialogShowTime =
              response.data.data.popup_gh_desktop_download.show_time
            //计算属性是调用的时候执行，当配置文件准备完毕后即可执行
            setTimeout(() => {
              this.PcDialog = this.needPcDialog
            }, 3 * 1000)
          }
        })
        .catch(() => {})
    },
    //判断edge浏览器
    isEdgePlatform() {
      return process.env.VUE_APP_TYPE == "GH_EDGE"
    },
    getRestTimeMinutes() {
      if (this.connectRest == -1) {
        return ""
      }
      return this.connectRest
    },
    async getCurRestTime() {
      //上次使用日期
      let lastUseData = await browser.storage.local.get({ date: "" })

      //今天日期
      let today = new Date()
      let curDate = `${today.getFullYear()}-${
        today.getMonth() + 1
      }-${today.getDate()}`
      if (lastUseData && lastUseData.date && lastUseData.date == curDate) {
        //获取剩余使用时间
        let restTime = await browser.storage.local.get({
          connectRest: 60,
        })
        this.connectRest = restTime.connectRest
        if (this.connectRest == 0) {
          this.disconnect()
        }
      } else {
        let connectTimeData = await browser.storage.local.get({
          connectMaxMinutes: 60,
        })
        this.connectRest = connectTimeData.connectMaxMinutes
        await browser.storage.local.set({
          date: curDate,
          connectRest: this.connectRest,
        })
      }
    },
  },
  computed: {
    // 判断是否已分享（分享后当月有效）
    isShared() {
      return true
    },
    // 判断是否需要弹出分享弹窗
    needPopup() {
      if (!this.extInfo.popupTime) {
        return true
      }
      const popupTimeO = new Date(this.extInfo.popupTime)
      return this.today.getDate() !== popupTimeO.getDate()
    },
    // 判断是否需要弹出导流弹窗
    needPcDialog() {
      //如果还没打开过，则需要打开
      if (!this.extInfo.pcDialogTime) {
        return true
      }
      // 获取现在的时间戳，减去存储的时间戳，判断是否大于配置需要天数的时间戳
      const pcDialogTime = new Date().getTime()
      return (
        pcDialogTime - this.extInfo.pcDialogTime.getTime() >=
        this.pcDialogShowTime * 24 * 60 * 60 * 1000
      )
    },
    // 计算更新蒙层的高度
    updateAboveTableHeight() {
      const tableHeight = document.querySelector(
        "#table-proxies .el-table__body-wrapper"
      ).offsetHeight
      const rowHeight = document.querySelector(
        "#table-proxies .el-table__row"
      ).offsetHeight
      let height = tableHeight - rowHeight * 2
      console.debug("height: ", height)
      if (height > rowHeight) {
        return height
      }
      return rowHeight
    },
  },
  watch: {
    // 当分享弹窗弹出过后，记录下来时间
    popupDivShow() {
      if (this.popupDivShow) {
        browser.runtime.sendMessage({
          action: "sharePopupPopped",
          data: {},
        })
        this.$ga.event("share/show", "count").send()
      }
    },
    // 当导流弹窗弹出过后，记录下来时间
    PcDialog() {
      if (this.PcDialog) {
        browser.runtime.sendMessage({
          action: "pcDialogOpen",
          data: {},
        })
      }
      this.$ga.event("WebPopup_show_count", "show_count").send()

      this.getExtInfo()
    },
  },
  async created() {
    if (this.isEdgePlatform()) {
      this.getCurRestTime()
    }
    this.language = browser.i18n.getUILanguage()

    await this.init()
    setTimeout(() => {
      this.checkStartStatus()
    }, 60 * 1000)

    // setTimeout(() => {
    //   this.checkErrorForDnsTips()
    // }, 10*1000)
    // this.$ga.set('uid', this.extInfo.uid)
    browser.runtime.onMessage.addListener((msg) => {
      if (typeof msg === "object" && "action" in msg) {
        switch (msg.action) {
          case "disconnected":
            this.init().then()
            break
          case "ga_achieve_limited":
            // //统计达到限制
            this.$ga.event("day_connect_achieve_limited_time", "count").send()
            break
        }
      }
    })
    // eslint-disable-next-line no-unused-vars
    const self = this
    axios
      .get(
        `https://static.greenhubtx.ga/ghex/config.json?t=${new Date().getHours()}&uid=${
          this.extInfo.uid
        }&version=${this.extInfo.version}`
      )
      .then(async (response) => {
        let data = response.data
        self.ghDesktopDownloadPage = `${data.gh_desktop_download_page}?utm_source=gh&utm_medium=click&utm_campaign=options`

        if (self.isEdgePlatform()) {
          if (data.gh_edge_connect_max_time) {
            self.connectMaxMinutes = data.gh_edge_connect_max_time
          }
          if (data.gh_edge_connect_time) {
            self.connectEveryMinutes = data.gh_edge_connect_time
          }
          await browser.storage.local.set({
            connectMaxMinutes: self.connectMaxMinutes,
            connectEveryMinutes: self.connectEveryMinutes,
          })
        } else {
          if (data.gh_edge_connect_time) {
            self.connectEveryMinutes = data.gh_ce_connect_time
          }
          await browser.storage.local.set({
            connectEveryMinutes: self.connectEveryMinutes,
          })
        }

        if (!this.extinfo.connection) {
          //connection不存在，说明当前为未连接状态，需要重置seconds，连接时不能重置
          self.seconds = self.connectEveryMinutes * 60
        }
      })
      .catch(() => {})
    this.$ga.pageview("/option").send()
    this.$ga.event("options/open", "options_open").send()
    this.$ga.event("version", this.extInfo.version).send()
  },
  beforeDestroy() {
    clearInterval(this.timer)
    this.timer = null
  },
}
</script>

